@import url("https://fonts.googleapis.com/css2?family=Alata&family=Josefin+Sans:wght@300&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  width: 100%;
  display: block;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --white: 0 0% 100%;
  --black: 0 0% 0%;
  --dark-gray: 0 0% 55%;
  --very-dark-gray:0 0% 41%;
  --selection-bg: 277 100% 50%;
}

/**
 * ### Font family 
 */
/**
 * ### Size variables
 */
/**
 * ### Font weight
 */
::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

html {
  font-size: 100%;
  font-family: "Alata", sans-serif;
}

body {
  background-color: hsl(var(--white));
  font-size: 0.9375rem;
}

p:not([class]) {
  opacity: 0.7;
  max-width: 32ch;
}

/**
 * Colors
 */
.bg-white {
  background-color: hsl(var(--white));
}

.clr-white {
  color: hsl(var(--white));
}

.bg-black {
  background-color: hsl(var(--black));
}

/**
 * Position
 */
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/**
 * ### With container
 */
.container {
  --max-width: 1440px;
  --container-padding: 1rem;
  width: min(var(--max-width), 100% - var(--container-padding) * 0);
  margin-inline: auto;
}

/**
 * ### Flex 
 */
.flex {
  display: flex;
}

.flex-dir-col {
  flex-direction: column;
}

.justify-cont-betw {
  justify-content: space-between;
}

/**
 * ### Grid 
 */
.grid-columns {
  display: grid;
  align-content: space-around;
}
@media (min-width: 48.125em) {
  .grid-columns {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

/**
 * ### Gap 
 */
.gap-1 {
  gap: 1rem;
}

.gap-2 {
  gap: 2rem;
}

.gap-3 {
  gap: 3rem;
}

/**
 * Float
 */
.float-l {
  float: left;
}

.float-r {
  float: right;
}

/**
 * ### Align items center 
 */
.align-itm-center {
  align-items: center;
}

/**
 * ### Justify content center , start
 */
.justify-cont-center {
  justify-content: center;
}

.justify-cont-start {
  justify-content: start;
}

/**
 * ### Justify items center , start
 */
.justify-item-center {
  justify-items: center;
}

.justify-items-start {
  justify-items: start;
}

/**
 * ### Text  center 
 */
.text-align-center {
  text-align: center;
}

/**
 * Flow -Creating an upper edge for all descendants except the first.
 * inline style --> style="--flow-spacer: 1.5rem"
 */
:where(.flow :not(:first-child)) {
  margin-top: var(--flow-spacer, 1em);
}

/**
 * ### Margin Padding position
 */
.margin-bottom-700 {
  margin-block-end: 3rem;
}

/**
 * Padding top and bottom
 */
.padding-block-600 {
  padding-block: 2rem;
}

.padding-block-700 {
  padding-block: 3rem;
}

.padding-block-900 {
  padding-block: 5rem;
}

.padding-lr-400 {
  padding-inline: 1rem;
}

.padding-lr-600 {
  padding-inline: 2rem;
}

.padding-lr-800 {
  padding-inline: 4rem;
}

/**
 * ### Font Size
 */
.fs-400 {
  font-size: 1rem;
}

.fs-600 {
  font-size: 2rem;
}

.fs-700 {
  font-size: 3rem;
}

.btn {
  display: inline-flex;
  border: 0;
  padding: 0.84em 2.5em;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

header.primary-header {
  height: auto;
  position: fixed;
  top: 0;
  z-index: 1;
}

.nav-active {
  transition: 0.3s ease-in-out;
  background-color: black;
}

.primary-navigation {
  display: flex;
  width: 100%;
  max-width: 100vw;
  height: auto;
  justify-content: space-between;
}

.primary-navigation-wrapper {
  display: none;
}
@media (min-width: 48.125em) {
  .primary-navigation-wrapper {
    display: flex;
    width: 100%;
    height: auto;
    justify-content: flex-end;
    gap: 1.25rem;
  }
}

.primary-navigation-wrapper a {
  position: relative;
  text-decoration: none;
  font-size: 1.5625rem;
  color: hsl(var(--white));
}
.primary-navigation-wrapper a:hover::before, .primary-navigation-wrapper a:focus::before {
  position: absolute;
  content: "";
  top: 125%;
  left: 25%;
  width: 50%;
  height: 0.125rem;
  background-color: hsl(var(--white));
}

.sidenav {
  width: 0;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 3.75rem;
}
@media (min-width: 48.125em) {
  .sidenav {
    display: none;
  }
}

.sidenav .nav-links {
  position: relative;
  padding: 0.5rem 0.5rem 0.5rem 2rem;
  text-decoration: none;
  font-size: 1.5625rem;
  color: hsl(var(--white));
  display: block;
  transition: 0.3s;
  --link-width: 100%;
}
.sidenav .nav-links:hover::before, .sidenav .nav-links:focus::before {
  position: absolute;
  content: "";
  top: 100%;
  left: 10%;
  width: 10%;
  height: 0.125rem;
  background-color: hsl(var(--white));
}

.sidenav .primary-links {
  position: relative;
  padding: 0.5rem 0.5rem 0.5rem 2rem;
  text-decoration: none;
  font-size: 1.5625rem;
  color: hsl(var(--white));
  display: block;
  transition: 0.3s;
}
.sidenav .primary-links:hover::before, .sidenav .primary-links:focus::before {
  position: absolute;
  content: "";
  top: 100%;
  left: 10%;
  width: 50%;
  height: 0.125rem;
  background-color: hsl(var(--white));
}

a.nav-links-logo {
  position: absolute;
  top: 8%;
  left: 3%;
  display: flex;
  max-width: 200px;
}

a.closebtn {
  display: block;
  position: absolute;
  top: 8%;
  right: 3%;
  margin-left: 3.125rem;
  font-size: 2.5rem;
  cursor: pointer;
  color: hsl(var(--white));
  text-decoration: none;
  line-height: 1;
}
@media (min-width: 48.125em) {
  a.closebtn {
    display: none;
  }
}

span.openbtn {
  font-size: 1.875rem;
  cursor: pointer;
  display: block;
  line-height: 1;
  color: hsl(var(--white));
}
@media (min-width: 48.125em) {
  span.openbtn {
    display: none;
  }
}

header.primary-header .title {
  display: grid;
  padding-inline: 1.25rem;
}
@media (min-width: 48.125em) {
  header.primary-header .title {
    padding-inline: 0;
    text-align: left;
    grid-template-columns: repeat(2, 1fr);
  }
}

header.primary-header h1 {
  grid-column: 1/2;
  padding: 1.125rem;
  font-size: 2.375rem;
  font-family: "Josefin Sans", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1.6px;
  line-height: 1.1;
  color: hsl(var(--white));
  border: 2px solid hsl(var(--white));
}
@media (min-width: 48.125em) {
  header.primary-header h1 {
    padding: 1.5625rem;
    font-size: 2.625rem;
  }
}
@media (min-width: 62.5em) {
  header.primary-header h1 {
    padding: 2.5rem;
    font-size: 3.625rem;
  }
}
@media (min-width: 80em) {
  header.primary-header h1 {
    padding: 3.125rem;
    font-size: 3.875rem;
  }
}

section.hero {
  background-image: url("/images/mobile/image-hero.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  z-index: -1;
}
@media (min-width: 48.125em) {
  section.hero {
    background-image: url("/images/desktop/image-hero.jpg");
  }
}

section.hero .hero-title {
  display: flex;
}
@media (min-width: 48.125em) {
  section.hero .hero-title {
    padding-inline: 0;
  }
}

section.hero .hero-title h1 {
  margin-top: 4.375rem;
  max-width: 31.25rem;
  padding: 0.9375rem;
  font-size: 1.75rem;
  font-family: "Josefin Sans", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1.6px;
  line-height: 1.1;
  color: hsl(var(--white));
  border: 2px solid hsl(var(--white));
}
@media (min-width: 29.375em) {
  section.hero .hero-title h1 {
    margin-top: 6.25rem;
    padding: 1.125rem;
    font-size: 2.875rem;
  }
}
@media (min-width: 48.125em) {
  section.hero .hero-title h1 {
    margin-top: 4.375rem;
    padding: 1.25rem;
    font-size: 2.875rem;
  }
}
@media (min-width: 62.5em) {
  section.hero .hero-title h1 {
    margin-top: 6.25rem;
    padding: 1.25rem;
    font-size: 3.25rem;
  }
}

.testimonials-grid {
  text-align: center;
}
@media (min-width: 48.125em) {
  .testimonials-grid {
    display: grid;
    text-align: left;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}

.testimonials-picture {
  grid-column: 1/5;
  grid-row: 1/4;
}

.testimonials-text {
  background-color: hsl(var(--white));
  grid-column: 4/7;
  grid-row: 2/4;
  padding: 0;
  margin-top: 3.125rem;
}
@media (min-width: 48.125em) {
  .testimonials-text {
    margin-top: 0;
    padding-top: 5rem;
    padding-left: 5rem;
  }
}

.testimonials-title {
  font-size: clamp(2.25rem, 1rem + 2vw, 3.25rem);
  font-family: "Josefin Sans", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1.6px;
  line-height: 1.1;
}

.testimonials-description {
  color: hsl(var(--dark-gray));
}

.grid-card {
  display: grid;
  grid-template-rows: 3fr;
}
@media (min-width: 48.125em) {
  .grid-card {
    display: flex;
    flex-direction: column;
  }
}

.creation {
  text-align: center;
  grid-row: 1;
  font-family: "Josefin Sans", sans-serif;
  text-transform: uppercase;
  font-size: clamp(2.25rem, 1rem + 2vw, 3.25rem);
}
@media (min-width: 48.125em) {
  .creation {
    align-self: flex-start;
  }
}

.btn-grid {
  grid-row: 3;
  justify-self: center;
  margin-top: 0;
}
@media (min-width: 48.125em) {
  .btn-grid {
    align-self: flex-end;
    margin-top: -50px;
  }
}

.btn-grid button {
  letter-spacing: 1.6px;
  font-family: "Josefin Sans", sans-serif;
  border: 1px solid hsl(var(--black));
  background-color: hsl(var(--white));
  color: hsl(var(--black));
  text-transform: uppercase;
}
.btn-grid button:hover, .btn-grid button:focus {
  color: hsl(var(--white));
  background-color: hsl(var(--black));
}

.card-picture {
  position: relative;
  width: 100%;
  height: 100%;
  color: hsl(var(--white));
  transition: 250ms ease-in-out;
}
.card-picture:hover, .card-picture:focus {
  filter: grayscale(100%);
}

.card-picture figcaption {
  position: absolute;
  bottom: 5%;
  left: 12%;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.625rem;
  text-transform: uppercase;
}
@media (min-width: 29.375em) {
  .card-picture figcaption {
    font-size: 2.375rem;
  }
}
@media (min-width: 48.125em) {
  .card-picture figcaption {
    font-size: 1.5rem;
  }
}
@media (min-width: 62.5em) {
  .card-picture figcaption {
    font-size: 2.125rem;
  }
}
@media (min-width: 80em) {
  .card-picture figcaption {
    font-size: 2.375rem;
  }
}

footer {
  display: flex;
}

.footer-wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 48.125em) {
  .footer-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer-box {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 48.125em) {
  .footer-box {
    align-items: flex-start;
    display: flex;
  }
}

.footer-links {
  display: flex;
  flex-direction: column;
}
@media (min-width: 48.125em) {
  .footer-links {
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
  }
}

.logos a,
.footer-links a {
  color: hsl(var(--white));
  text-decoration: none;
}

.logos-links {
  line-height: 0;
  font-size: 1.875rem;
}

a.logos-links {
  position: relative;
  text-transform: uppercase;
  --link-width: 100%;
  width: var(--link-width);
}
a.logos-links:hover::before, a.logos-links:focus::before {
  position: absolute;
  content: "";
  border-radius: 100vmax;
  top: 125%;
  left: 13%;
  width: calc(var(--link-width) - 30%);
  height: 0.125rem;
  background-color: hsl(var(--white));
}

.footer-links a {
  position: relative;
  text-transform: uppercase;
  text-align: center;
}
.footer-links a:hover::before, .footer-links a:focus::before {
  position: absolute;
  content: "";
  border-radius: 100vmax;
  top: 125%;
  left: 25%;
  width: 50%;
  height: 0.125rem;
  background-color: hsl(var(--white));
}

:where(.flow-footer :not(:first-child)) {
  margin-top: var(--flow-spacer, 1em);
}
@media (min-width: 48.125em) {
  :where(.flow-footer :not(:first-child)) {
    margin-top: 0;
  }
}

.logos {
  align-items: flex-end;
  display: flex;
  gap: 1.5rem;
}

.footer-figure {
  width: 200px;
}